import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';

import NavBar from '../../components/Navbar/Navbar';
import {FormControl, Row, Col, Card} from 'react-bootstrap';

import './home_style.css'
import { Link } from 'react-router-dom';

class Home extends React.Component {
    state = {
        loading: true,
        items: null,
        filteredItems: null,
        search: ''
    };

    async componentDidMount() {
        const url = "/db_items";
        const response = await fetch(url);
        const data = await response.json();
        this.setState({items: data, filteredItems: data, loading:false})
        console.log(data);
    }

    updateSearch(event) {
        this.setState({search: event.target.value});
    }

    render() {
        if (this.state.filteredItems != null && this.state.search != null) {
            this.state.filteredItems = this.state.items.filter(
                (item) => {
                    return item.name.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1;
                }
            );
        }

        return (
            <body>
                <NavBar/>
                <div>
                    <div class="inventory">
                        <div class="title">
                            Inventory 
                            <Link to="/add-items"><span class="fa fa-plus"/></Link>
                        </div>
                        <div class="form">
                            <div class="search">
                                <span class="fa fa-search"/>
                                <FormControl type="text" placeholder="Search" onChange={this.updateSearch.bind(this)}/>
                            </div>
                            
                        </div>
                        <div class="row_cont">
                            {this.state.loading || !this.state.items ? (
                                <Row />
                            ) : (
                                <Row>
                                    {this.state.filteredItems.map((item, index)=>{
                                        let expirationDate = '';
                                        (item.expirationDate != null) ? (expirationDate = item.expirationDate) : (expirationDate = "N/A")
                                        return (
                                            <Col xs={12} sm={10} md={6} lg={4}>
                                                <div class="cell">
                                                    <Card>
                                                        <Card.Img variant="top" src={item.image} />
                                                        <Card.Body>
                                                            <Card.Title>{item.name}</Card.Title>
                                                            <Card.Text>
                                                            Quantity: {item.quantity}
                                                            {/* <br/>Exp. Date: {expirationDate}*/}
                                                            </Card.Text>
                                                        </Card.Body>
                                                    </Card>
                                                </div>
                                            </Col>
                                        )
                                    })}
                                </Row>
                            )}
                        </div>
                    </div>
                </div> 
            </body>
        );
    }

}

export default Home;