import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';

import NavBar from '../../components/Navbar/Navbar';
import {Image, Row, Col} from 'react-bootstrap';

import './shoppingList_style.css'

class ShoppingList extends React.Component {
    state = {
        loading: true,
        shoppingList: null
    };

    async componentDidMount() {
        const url = "/db_items";
        const response = await fetch(url);
        const data = await response.json();
        this.setState({shoppingList: data, loading:false})
        console.log(data);
    }

    render() {
        return (
            <body>
                <NavBar />
                <div>
                    <div class="shopping-list">
                        <div class="title">
                            Shopping List
                            <span class="fa fa-plus"/>
                        </div>

                        <div class="row_cont">
                            {this.state.loading || !this.state.shoppingList ? (
                                <Row />
                            ) : (
                                <Row>
                                    {this.state.shoppingList.map((item, index)=>{
                                        let expirationDate = '';
                                        (item.expirationDate != null) ? (expirationDate = item.expirationDate) : (expirationDate = "N/A")
                                        return (
                                            <Row>
                                                <div class="cell">
                                                    <Row class="item">
                                                        <Col xs={11} sm={7} md={5} lg={3}>
                                                        <Image src={item.image} rounded fluid/>
                                                        </Col>
                                                        <Col>
                                                            <h3>{item.name}</h3>
                                                            <p>Quantity to buy: {item.quantity}
                                                            <br/>Exp. Date: {expirationDate} </p>
                                                        </Col>
                                                    </Row>
                                                </div>
                                                    {/*<div class="item">
                                                        <div class="image">
                                                            <Image src={item.image} />
                                                        </div>
                                                        
                                                        <div class="details">
                                                        {item.name}
                                                        <br/>Quantity: {item.quantity}
                                                        <br/>Exp. Date: {expirationDate}
                                                        </div>
                                                    </div>*/}
                                                
                                            </Row>
                                        )
                                    })}
                                </Row>
                            )}
                        </div>
                    </div>
                </div>
                
            </body>
        );
    }
}

export default ShoppingList;